import * as React from 'react'
declare global {
  interface Window {
    BinanceChain: any
    ethereum: any
  }
}

const convertParams = (params: { [key: string]: any }) => {
  const result = {}
  Object.keys(params).forEach((key) => {
    var snakeCase = key.replace(
      /[A-Z]/g,
      (letter) => `_${letter.toLowerCase()}`
    )
    result[snakeCase] = params[key]
  })
  return result
}

const rootUrl =
  process.env.REACT_APP_ROOT_URL || 'https://widget.arken.finance/'
// const rootUrl = 'http://localhost:3002'
const widget = {
  create: function (props: any) {
    const iframe = document.createElement('iframe')

    iframe.id = 'arken-widget'
    iframe.src = rootUrl + '?mode=' + props.mode + '&type=' + props.widgetType
    iframe.style.position = 'absolute'
    iframe.style.width = '100%'
    iframe.style.height = '100%'
    iframe.style.border = '0'

    iframe.addEventListener('load', async () => {
      console.log('iframe load complete')
      let originWalletAddress = null
      try {
        if (window.ethereum.selectedAddress) {
          originWalletAddress = window.ethereum.selectedAddress
        } else {
          originWalletAddress = await window.BinanceChain.requestAddresses()
        }
      } catch (error) {}

      const params = JSON.stringify({
        ...convertParams(props),
        origin: window?.location?.origin,
        originWalletAddress
      })
      console.log(params)

      if (iframe.contentWindow) {
        iframe.contentWindow.postMessage(`setupWidget:data:${params}`, rootUrl)
      }
    })
    return iframe
  },
  load: function (ele: HTMLDivElement, props: any): HTMLIFrameElement | null {
    if (ele) {
      const iframe = widget.create(props)
      ele.appendChild(iframe)
      return iframe
    }
    return null
  }
}

type WidgetType = 'swap' | 'graph' | 'graph-swap'
interface Props {
  mode?: string
  type?: string
  chain?: string
  themeColor?: string
  themeTextColor?: string
  backgroundColorLight?: string
  backgroundColorDark?: string
  baseTokenAddress?: string
  quoteTokenAddress?: string
  externalTopTokenAddress?: string[]
  graphRange?: number
  enableGraph?: boolean
  widgetType?: WidgetType
  maxWidth?: string
  height?: string
  containerStyle?: any
}

export const ArkenWidget = ({ containerStyle = {}, ...props }: Props) => {
  const ref = React.useRef<HTMLDivElement>(null)
  const iframeRef = React.useRef<HTMLIFrameElement | null>(null)

  React.useEffect(() => {
    if (!ref.current) {
      return
    }
    const iframe = iframeRef.current
    if (iframe?.contentWindow) {
      iframe.contentWindow.postMessage(`updateWidget:toggleTheme:`, rootUrl)
    } else {
      iframeRef.current = widget.load(ref.current, props)
    }
  }, [props.mode])

  return (
    <div
      ref={ref}
      id='arken-root'
      style={{ minHeight: 700, position: 'relative', ...containerStyle }}
    />
  )
}
